.ab_title {
  color: #09fbd3;
}
.ab_strong {
  background-color: #09fbd3;
}
.ab_container {
  margin-bottom: 1vw;
}
.ab_table {
  width: 55vw;
  height: 500px;
  padding: 2.5vw 1vw;
  border: 4px solid #09fbd3;
  color: black;
  background-color: #fff;
  position: relative;
  z-index: -1;
}
.ab_img {
  width: 35vw;
  height: 35vw;
  border: none;
}
.ab_discription {
  position: absolute;
  top: 60%;
  transform: translateY(-60%);
}

@media (max-width: 959px) {
  .ab_table { 
    width: 88vw;
    height: 450px;
  }
  .ab_img {
    width: 43vw;
    height: 43vw;
  }
}
@media (max-width: 599px) {
  h2{
    font-size: 1.25rem;
  }
  h3 {
    font-size :1rem;
  }
  .ab_table {
    height: 320px;
  }
}
